<template>
  <div class="class_hour_edit">
    <head-top title="课时编辑">
      <span slot="right" @click="saveData">
           <van-icon name="success" class="head-right"></van-icon>
      </span>
    </head-top>
    <van-cell-group>
      <van-field required placeholder="课时名称" maxlength="60" v-model="classHourName" :error-message="classHourNameErr"/>
    </van-cell-group>
  </div>
</template>

<script>
export default {
  name: "subject-edit",
  data () {
    return {
      subjectID: this.$store.getters.subjectID,
      classHourID: this.$store.getters.classHourID || "",
      classHourName: this.$store.getters.classHour ? this.$store.getters.classHour.name : "",
      classHourNameErr: ""
    };
  },
  methods: {
    saveData () {
      this.classHourNameErr = "";
      if (this.classHourName === "") {
        this.classHourNameErr = "请输入课时名称";
        return;
      }
      this.$mainHttp.classHour.dataAdd({
        chName: this.classHourName,
        chID: this.classHourID,
        courseID: this.subjectID
      }).then(res => {
        if (res.Result) {
          this.$toast("保存成功");
          this.$router.replace("/subject/view");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("保存失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../styles/common/base";
.class_hour_edit {

}
</style>
